@charset "UTF-8";
.host-location {
  font-size: 14px;
  line-height: 16px; }

.details_name, .descriptive_text, .contact-host, .amenities_item, .booking_price, .apartment_type, .host-description {
  font-size: 16px;
  line-height: 19px; }

h2, .details_date {
  font-size: 20px;
  line-height: 23px; }

.review_hosted_font {
  font-size: 28px;
  line-height: 33px; }

.booking_input, .booking_button {
  width: 270px;
  height: 50px;
  background: white;
  border-radius: 3px;
  font-size: 16px;
  line-height: 19px; }

.footer-column-item-title, .footer-column-title {
  text-decoration: none;
  color: #3d4e61; }

.booking-info-block, .main-container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: Avenir;
  /* Имя шрифта */
  src: url(../fonts/Avenir/Avenir-Book.ttf);
  /* Путь к файлу со шрифтом */ }

/*
h1 {
  @extend %h1;
}
*/
body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: Avenir, sans-serif;
  color: #3d4e61; }

h1 {
  font-size: 36px;
  line-height: 42px;
  color: #3d4e61;
  margin-top: 0; }

h2 {
  color: #3d4e61; }

a {
  text-decoration: none;
  color: #3d4e61; }

.is-bold {
  font-weight: bold; }

.review {
  display: flex;
  width: 491px;
  flex-direction: row;
  flex-wrap: wrap; }

.review_details {
  display: flex;
  flex-direction: row;
  width: 100%; }

.review_details_photo_wrapper {
  display: flex;
  margin-right: 21px; }

.review_photo {
  width: 50px;
  height: 50px; }

.details_name_date {
  width: 173px;
  height: 28px;
  margin: 2px; }

.details_name {
  font-weight: bolder; }

.details_date {
  font-weight: 500; }

.descriptive_text {
  width: 491px;
  height: 120px;
  color: #3d4e61; }

.header-container {
  height: 60px;
  background: white;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  justify-items: center;
  align-items: center; }
  .header-container .header-left {
    padding-left: 26px; }
    .header-container .header-left .header-small-logo-img {
      width: 30px;
      height: 30px; }
  .header-container .header-right {
    margin-right: 19px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between; }
    .header-container .header-right .header-nav-wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      justify-items: center;
      align-items: center; }
      .header-container .header-right .header-nav-wrapper .nav-wrap .nav-item {
        list-style: none;
        text-decoration: none;
        display: inline; }
        .header-container .header-right .header-nav-wrapper .nav-wrap .nav-item a {
          font-size: 16px;
          line-height: 22px;
          color: #3d4e61;
          text-decoration: none;
          margin-right: 30px; }
    .header-container .header-right .header-profile-wrapper {
      display: flex;
      justify-content: center;
      align-content: center; }
      .header-container .header-right .header-profile-wrapper .header-logo {
        width: 30px;
        height: 30px;
        border-radius: 50%; }

.main_photo {
  width: 100%;
  height: 464px;
  object-fit: cover; }

button.contact-host {
  border: 1px solid #118488;
  box-sizing: border-box;
  border-radius: 4px;
  width: 150px;
  height: 50px;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #118488;
  margin-bottom: 75px;
  position: absolute;
  left: 0;
  bottom: 0; }

.contact-host {
  margin-top: 5px;
  margin-bottom: 30px; }

.amenities_container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 352px; }

.amenities_item {
  width: 50%;
  height: 29px;
  color: #3d4e61; }

.booking-info-block {
  position: absolute;
  right: 0;
  top: 0;
  width: 330px;
  height: 320px;
  background: white;
  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  border-radius: 3px; }

.booking_input_wrapper {
  margin: 15px auto; }

.booking_input {
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  padding-left: 17px; }

.booking_button {
  text-align: center;
  background: #fd5c63;
  color: white; }

.booking_price {
  margin-left: 30px;
  margin-bottom: 24px;
  width: 112px;
  height: 22px; }

