.apartment_type {
  display: inline;
  margin-right: 25px;
  color: #3d4e61; }

.review_hosted_font {
  font-family: "Roboto", sans-serif; }

.host-location {
  color: #3d4e61; }

.host-description {
  color: #3d4e61;
  width: 491px;
  height: 100px;
  margin: 0; }

.main-info-block {
  position: relative;
  width: 900px;
  height: 1265px;
  margin: 40px auto 75px; }

.footer-column-title {
  font-weight: bold; }

.footer-item {
  margin-top: 10px; }

footer {
  background: white;
  box-shadow: 0 -1px 0 #e1e7ed;
  display: flex; }
  footer .footer-container {
    margin: 44px auto;
    display: grid;
    width: 901px;
    grid-template-columns: repeat(3, auto);
    font-size: 16px;
    line-height: 19px;
    color: #3d4e61; }
